import React, { Fragment, useState } from "react";
import { Loading, Article as Art } from "components";

const Article = (props) => {
  const [loading] = useState(false);

  if (!!loading) {
    return <Loading />;
  }

  return (
    <Fragment>
      <Art image="images/dark-souls-3.jpg" overline="article" title="Article title" caption="article caption" />
    </Fragment>
  );
};

export default Article;
